.App {
  background-color: #434343;
}
.App.scrolling .App-header {
  -webkit-transition: background-color 0.8s;
  -o-transition: background-color 0.8s;
  transition: background-color 0.8s;
  background-color: #333333;
}
.App-header {
  background-color: rgba(0, 0, 0, 0);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  color: white;
  z-index: 999;
}
.bars {
  position: absolute;
  top: 26px;
  left: 40px;
  width: 48px;
  height: 48px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 998;
  opacity: 0;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
}
.bars > div {
  position: absolute;
  left: 0;
  right: 0;
  width: 8px;
  height: 28px;
  background-color: #ffffff;
  -webkit-transform: translate(0, 50%);
      -ms-transform: translate(0, 50%);
          transform: translate(0, 50%);
  -webkit-transition: left 0.5s cubic-bezier(0.19, 1, 0.22, 1), color 0.3s,
    -webkit-transform 1s cubic-bezier(0.145, 0.91, 0.14, 0.995);
  transition: left 0.5s cubic-bezier(0.19, 1, 0.22, 1), color 0.3s,
    -webkit-transform 1s cubic-bezier(0.145, 0.91, 0.14, 0.995);
  -o-transition: transform 1s cubic-bezier(0.145, 0.91, 0.14, 0.995),
    left 0.5s cubic-bezier(0.19, 1, 0.22, 1), color 0.3s;
  transition: transform 1s cubic-bezier(0.145, 0.91, 0.14, 0.995),
    left 0.5s cubic-bezier(0.19, 1, 0.22, 1), color 0.3s;
  transition: transform 1s cubic-bezier(0.145, 0.91, 0.14, 0.995),
    left 0.5s cubic-bezier(0.19, 1, 0.22, 1), color 0.3s,
    -webkit-transform 1s cubic-bezier(0.145, 0.91, 0.14, 0.995);
}

.App-header .bars div:nth-child(1) {
  left: 0;
}
.App-header .bars div:nth-child(2) {
  left: 15px;
}
.App-header .bars div:nth-child(3) {
  left: 30px;
}

.App-header.show-nav .bars > div {
  margin: 0;
  background-color: $white;
}
.App-header.show-nav .bars div:nth-child(1) {
  left: 50%;
  -webkit-transform: translate(0, 50%) rotate(135deg);
      -ms-transform: translate(0, 50%) rotate(135deg);
          transform: translate(0, 50%) rotate(135deg);
}
.App-header.show-nav .bars div:nth-child(2) {
  opacity: 0;
}
.App-header.show-nav .bars div:nth-child(3) {
  left: 50%;
  -webkit-transform: translate(0, 50%) rotate(-135deg);
      -ms-transform: translate(0, 50%) rotate(-135deg);
          transform: translate(0, 50%) rotate(-135deg);
}

.App-header .nav {
  background-color: #333333;
  width: 0;
  height: 0;
  overflow: hidden;
  -webkit-transition: width 1s cubic-bezier(0.165, 0.84, 0.44, 1),
    height 1s cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: width 1s cubic-bezier(0.165, 0.84, 0.44, 1),
    height 1s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: width 1s cubic-bezier(0.165, 0.84, 0.44, 1),
    height 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.App-header .nav .nav-wrap {
  padding: 100px 40px 40px;
}
.App-header.show-nav .nav {
  width: 600px;
  height: 600px;
}
.nav .nav-wrap .large {
  padding: 20px 0;
}
.nav .nav-wrap .large .nav-item span {
  font-size: 52px;
  font-weight: 700;
  line-height: 1.4em;
  color: #999999;
}
.nav .nav-wrap .small .nav-item span {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.4em;
  color: #999999;
}
.nav .nav-wrap .social {
  height: 24px;
  padding: 40px 0;
}
.nav .nav-wrap .social svg {
  height: 100%;
  width: auto;
  margin: 0 5px;
}
.nav .nav-wrap .social svg path {
  fill: #999999;
}
.open-design .nav .large .nav-item.nav-design span,
.open-projects .nav .large .nav-item.nav-projects span,
.open-people .nav .large .nav-item.nav-people span {
  color: white;
}
.header-logo {
  position: absolute;
  right: 40px;
  top: 20px;
  height: 40px;
  width: 80px;
  z-index: 990;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  opacity: 0;
}

.footer-logo {
  position: absolute;
  bottom: 20px;
  right: 40px;
  height: 60px;
  z-index: 990;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  opacity: 1;
}

.open .footer-logo {
  opacity: 0;
}
.open .header-logo {
  opacity: 1;
}
.open .bars {
  opacity: 1;
}

.header-logo svg {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
